body {
    font-family: "Poppins";
    font-size: 18px;
    margin: 0;
    height: 100vh;
    /* background: #161617; */
    color: #fff;
  }
  
  :root {
    --color-primary: #4b6aff;
  }
  
  ul {
    margin: 0;
    padding: 0;
    list-style: none;
  }
  
  nav {
    display: flex;
    align-items: center;
    /* justify-content: space-between; */
    padding: 10px 30px 0;
    background-color: #fff;
    box-shadow: 0px 4px 8px rgba(0,0,0,0.1);
    

  }
  
  nav > :is(h1, span.material-symbols-outlined) {
   
    
    width: 34px;
    height: 34px;
    display: grid;
    place-items: center;
  }
  
  nav h1 {
    background: #343950;
    border-radius: 8px;
    font-size: 16px;
    width: 34px;
    height: 34px;
    display: grid;
    place-items: center;
  
  }
  
  nav span {
    color: #fff;
    font-size: 20px;
  }
  
  a {
    cursor: pointer;
  }
  
  .menu {
    margin: 0;
    padding: 0;
    display: flex;
    /* justify-content: center; */
    /* justify-content: space-between; */
    /* align-items: stretch; */
    height: 70px;
    z-index: 999;
   
    /* width: 50%; */
  }
  /* .sticky-nav {
    position: fixed;
    top: 0;
    left: 0;
} */
  
  .menu li {
    display: flex;
    margin-right: 10px;
    /* width: 80px; */
    color: #999;
    transition: all 0.3s ease-in-out;

  }
  
  .menu li:hover .submenu {
     height: auto;
     

    }
  
  /* .menu > li:hover > a {
    color: #fff;
  } */
  
  .menu > li:hover > a::before {
    visibility: visible;
    scale: 1 1;
  }
  
  .submenu {
    overflow: hidden;
    position: absolute;
  
    display: flex;
    top: 8vh;
    
    /* justify-content: center; */
    /* align-items: stretch; */
    box-shadow: 0px 4px 8px rgba(0,0,0,0.1);
    /* flex-direction: row; */
    animation: fadeIn 0.5s;
    animation-fill-mode: forwards;
    background-color: #fff;
    height: 0;
    /* line-height: 40px; */
    /* box-sizing: border-box; */
    transition: height 0.3s ease-in-out;
   
 
  }

  
  .menu li .submenu a {
    color:black;
    opacity: 0;
    font-size: 16px;
    transition: opacity 0.25s;
  }
  
  .menu > li:hover .submenu a {
    opacity: 1;
    color:black;

  }
  
  /* .menu > li .submenu a:hover {
    background: rgb(0 0 0 / 20%);
  } */
  
  .menu > li a {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 7px;
    font-size: 16px;
    text-decoration: none;
    position: relative;
    height: 100%;
    transition: 0.3s;
  }
  
  .menu > li > a::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 3px;
    bottom: 3px;
    left: 0px;
    background-color: var(--color-primary);
    transition: all 0.2s ease-in-out;
    scale: 0 0;
    visibility: hidden;
  }

  .dashboard-search-bar{
    display:flex;
    font-size: 14px;
    /* /* height: 20px; */
    line-height: 24px;
    /* color: #696e79; */
