  }
  
  .inputStyle{
    outline: 0;
    border: 0px;
    background: #f5f5f6;
    color: #696e79;
/* background-color: red; */
    width: 100%;
  }

  .seperator{
    border-bottom: 2px solid lightgray;
    height: 1px;
  }

  

 

  .sub_section_title:hover{
    font-weight: 700;
  }

  .profile-nav-bar{
    display: flex;
    width: 18%;
    justify-content: flex-end;
  }

  .profile-nav-bar-icon{
cursor: pointer;
color: #343950;
/* margin-right: 5px; */
